.wrapper {
  width: 100%;
  height: 100%;
  background: #ffffff;
}
.table {
  height: calc(100% - 32px);
  &.without_pagination {
    height: 100%;
  }
  :global {
    .ant-table-wrapper {
      height: 100%;
      .ant-spin-nested-loading {
        height: 100%;
        div {
          .ant-spin {
            max-height: 100%;
          }
        }
        .ant-spin-container {
          height: 100%;
          .ant-table {
            height: 100%;
            &.ant-table-middle {
              .ant-table-filter-column-title {
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                word-break: keep-all;
              }
              .ant-table-filter-column {
                margin: -16px -16px;
              }
            }
            &.ant-table-empty {
              .ant-table-body {
                max-height: 0 !important ;
              }
            }
            &.ant-table-bordered {
              thead {
                tr {
                  th {
                    &:last-child {
                      border-right: 0px;
                    }
                  }
                }
              }
              tbody {
                tr {
                  td {
                    &:last-child {
                      border-right: 0px;
                    }
                  }
                }
              }
            }
            .ant-table-container {
              height: 100%;
              display: flex;
              flex-direction: column;
              border-top-left-radius: 0;
              border-top-right-radius: 0;
              .ant-table-content {
                overflow: hidden !important;
              }
              .ant-table-header {
                overflow: hidden !important ;
              }
              table {
                border-radius: 0;
                thead {
                  tr {
                    &:first-child {
                      th {
                        &:first-child {
                          border-top-left-radius: 0;
                        }
                        &:last-child {
                          border-top-right-radius: 0;
                        }
                      }
                    }
                  }
                }
              }
              .ant-table-content {
                height: 100%;
                table {
                  height: 100%;
                  .ant-table-placeholder {
                    .ant-table-cell {
                      border: unset;
                    }
                  }
                }
              }
              .ant-table-body {
                flex: 1;
                overflow: hidden !important;
                .ant-table-tbody {
                  .ant-table-row {
                    cursor: pointer;
                    &.not_viewed {
                      font-weight: bold;
                    }
                  }
                }
              }
              // .ant-table-scroll {
              //   height: 100%;
              //   display: flex;
              //   flex-direction: column;
              //   .ant-table-header {
              //     background: #fafafa;
              //     overflow-y: hidden !important;
              //   }
              //   .ant-table-body {
              //     flex: 1;
              //     .ant-table-row {
              //       cursor: pointer;
              //       &.not_viewed {
              //         font-weight: bold;
              //       }
              //     }
              //   }
              //   .ant-table-placeholder {
              //     flex: 1;
              //   }
              // }
            }
          }
        }
      }
    }
  }
}
